.postsBlock {
    padding: 10px;
}

.posts {
    margin-top: 10px;
    
}

.photo {
    width: 50px;
}

.post-enter {
    transform: translateX(0px);
}
.post-enter-active {
  transform: translateX(350px);
  transition: all 500ms ease-in;
}
.post-exit {
  opacity: 1;
}
.post-exit-active {
  transform: translateX(-350px);
  transition: all 500ms ease-in;
}

.page__wrapper {
  margin-top: 30px;
  display: flex;
  justify-content: center;
}

.page {
  padding: 10px;
  border: 1px solid darkgray;
  cursor: pointer;
}

.page__current {
  border: 2px solid black;
  font-weight: bold;
}